body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.machine {
  width: 50px;
  height: 50px;
  display: inline-block;
  margin: 4px;
  border: 1px solid rgb(0, 0, 0);
}

.machines {
  background-color: rgb(82, 82, 80);
  width: 192px;
  height: 380px;
  border: 1px solid rgb(238, 241, 6);
  border-radius: 4px;
  margin: 20px;
}

.selected {
  width: 50px;
  height: 50px;
  display: inline-block;
  margin: 4px;
  border: 2px solid rgb(216, 210, 124);
}

h1, h2, h5 {
  color: yellow;
  margin: 10px;
  margin-top: 30px;
}
.dpiece {
  width: 55px;
  height: 55px;
  display: inline-table;
  margin: 0px;
  margin-top: -7px;
  border: 1px solid rgb(0, 0, 0);
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
}

.lpiece {
  width: 55px;
  height: 55px;
  display: inline-table;
  margin: 0px;
  margin-top: -7px;
  border: 1px solid rgb(0, 0, 0);
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
}

.upiece {
  width: 55px;
  height: 55px;
  display: inline-table;
  margin: 0px;
  margin-top: -7px;
  border: 1px solid rgb(0, 0, 0);
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg);
}

.rpiece {
  width: 55px;
  height: 55px;
  display: inline-table;
  margin: 0px;
  margin-top: -7px;
  border: 1px solid rgb(0, 0, 0);
  -webkit-transform: rotate(270deg);
  transform: rotate(270deg);
}

.dselected {
  width: 55px;
  height: 55px;
  display: inline-table;
  margin: 0px;
  margin-top: -7px;
  border: 1px solid rgb(240, 232, 117);
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
}

.lselected {
  width: 55px;
  height: 55px;
  display: inline-table;
  margin: 0px;
  margin-top: -7px;
  border: 1px solid rgb(240, 232, 117);
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
}

.uselected {
  width: 55px;
  height: 55px;
  display: inline-table;
  margin: 0px;
  margin-top: -7px;
  border: 1px solid rgb(240, 232, 117);
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg);
}

.rselected {
  width: 55px;
  height: 55px;
  display: inline-table;
  margin: 0px;
  margin-top: -7px;
  border: 1px solid rgb(240, 232, 117);
  -webkit-transform: rotate(270deg);
  transform: rotate(270deg);
}

.dselected:hover {
  border: 1px solid rgb(255, 255, 255);
}

.lselected:hover {
  border: 1px solid rgb(255, 255, 255);
}

.uselected:hover {
  border: 1px solid rgb(255, 255, 255);
}

.rselected:hover {
  border: 1px solid rgb(255, 255, 255);
}

.dpiece:hover {
  border: 1px solid rgb(255, 255, 255);
}

.lpiece:hover {
  border: 1px solid rgb(255, 255, 255);
}

.upiece:hover {
  border: 1px solid rgb(255, 255, 255);
}

.rpiece:hover {
  border: 1px solid rgb(255, 255, 255);
}

.panel {
  width: 575px;
  height: 575px;
  display: inline-block;
  margin-top: 25px;
  margin: 30px;
}

.details {
  background-color: rgb(94, 94, 92);
  width: 470px;
  height: 290px;
  border: 1px solid rgb(147, 187, 187);
  border-radius: 4px;
  margin-top: 20px;
}

.marginH4 {
  margin: 5px;
}

.buttonTick {
  background-color: rgb(160, 160, 150);
}

.earnings {
  background-color: rgb(82, 82, 80);
  width: 192px;
  height: 60px;
  border: 1px solid rgb(238, 241, 6);
  border-radius: 4px;
  margin: 20px;
}

h1, h2, h5{
  color: yellow;
  margin: 10px;
  margin-top: 10px;
}

.buttonTick {
  margin: 20px;
  width: 192px;
  height: 30px;
  background-color: rgb(160, 160, 150);
}
.App {
  text-align: center;
}

h1, h2 {
  color: yellow;
  margin: 0;
  margin-top: 20px;
}

.grid-container {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: .2fr 1.8fr;
  grid-template-areas: "header-container" "body-container";
}

.header-container {
  display: grid;
  grid-area: header-container;
  justify-content: center;
  background-color: rgb(27, 27, 31);
}

.body-container {
  display: grid;
  grid-area: body-container;
  grid-template-columns: 0.3fr 1.4fr 0.3fr;
  grid-template-rows: 1fr;
  grid-template-areas: "machines earnings details";
}

